input[type="range"].simulator-slider {
  -webkit-appearance: none;
  width: 100%;
  margin: 10px 0;
}
input[type="range"].simulator-slider:focus {
  outline: none;
}
input[type="range"].simulator-slider::-webkit-slider-runnable-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  box-shadow: 1.7px 1.7px 7.8px rgba(0, 0, 0, 0.84),
    0px 0px 1.7px rgba(13, 13, 13, 0.84);
  background: rgba(0, 0, 0, 0.5);
  border-radius: 7.2px;
  border: 0.5px solid rgba(0, 0, 0, 0);
}
input[type="range"].simulator-slider::-webkit-slider-thumb {
  box-shadow: 1.6px 1.6px 8.5px rgba(0, 0, 0, 0.57),
    0px 0px 1.6px rgba(13, 13, 13, 0.57);
  border: 1.6px solid #000000;
  height: 30px;
  width: 20px;
  border-radius: 4px;
  background: #ffffff;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -10.5px;
}
input[type="range"].simulator-slider:focus::-webkit-slider-runnable-track {
  background: rgba(26, 26, 26, 0.5);
}
input[type="range"].simulator-slider::-moz-range-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  box-shadow: 1.7px 1.7px 7.8px rgba(0, 0, 0, 0.84),
    0px 0px 1.7px rgba(13, 13, 13, 0.84);
  background: rgba(0, 0, 0, 0.5);
  border-radius: 7.2px;
  border: 0.5px solid rgba(0, 0, 0, 0);
}
input[type="range"].simulator-slider::-moz-range-thumb {
  box-shadow: 1.6px 1.6px 8.5px rgba(0, 0, 0, 0.57),
    0px 0px 1.6px rgba(13, 13, 13, 0.57);
  border: 1.6px solid #000000;
  height: 30px;
  width: 20px;
  border-radius: 4px;
  background: #ffffff;
  cursor: pointer;
}
input[type="range"].simulator-slider::-ms-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type="range"].simulator-slider::-ms-fill-lower {
  background: rgba(0, 0, 0, 0.5);
  border: 0.5px solid rgba(0, 0, 0, 0);
  border-radius: 14.4px;
  box-shadow: 1.7px 1.7px 7.8px rgba(0, 0, 0, 0.84),
    0px 0px 1.7px rgba(13, 13, 13, 0.84);
}
input[type="range"].simulator-slider::-ms-fill-upper {
  background: rgba(0, 0, 0, 0.5);
  border: 0.5px solid rgba(0, 0, 0, 0);
  border-radius: 14.4px;
  box-shadow: 1.7px 1.7px 7.8px rgba(0, 0, 0, 0.84),
    0px 0px 1.7px rgba(13, 13, 13, 0.84);
}
input[type="range"].simulator-slider::-ms-thumb {
  box-shadow: 1.6px 1.6px 8.5px rgba(0, 0, 0, 0.57),
    0px 0px 1.6px rgba(13, 13, 13, 0.57);
  border: 1.6px solid #000000;
  height: 30px;
  width: 20px;
  border-radius: 4px;
  background: #ffffff;
  cursor: pointer;
  height: 10px;
}
input[type="range"].simulator-slider:focus::-ms-fill-lower {
  background: rgba(0, 0, 0, 0.5);
}
input[type="range"].simulator-slider:focus::-ms-fill-upper {
  background: rgba(26, 26, 26, 0.5);
}

.bg-navbar {
  background-color: rgba(0, 0, 0, 0.8);
}

