.about-paragraph {
  color: rgb(95, 95, 95);
  font-family: customBodyFont;
  display: block;
}

.welcome-header-text {
  font-family: customTitleFont;
}

.header-container {
  vertical-align: middle;
}

.welcome-image {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  margin-top: 5rem;
}

.welcome-text {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  margin-top: 5rem;
}

.welcome-sub-text {
  font-size: 1.75rem;
}

.welcome-header-image {
  /* The image used */
  /* background-image: url("../../../img/headerbanner.jpg");*/

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  min-height: 100vh;
  display: flex;
}

/* Small breakpoint */
@media screen and (max-width: 576px) {
  .welcome-image {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0rem;
    margin-top: 3.5rem;
  }

  .welcome-text {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0rem;
    margin-top: 0rem;
  }

  .welcome-header-text {
    padding: 1rem;
  }
}

/* Medium breakpoint */
@media screen and (max-width: 768px) {
}
